:root {
    --primaryBackgroundColor: #f6f9fc;
    --RCPG-primary-color: #f5455c;
    --RCPG-secondary-color: #19ac7c;
    --RCPG-tertary-color: #156ff5;
    --animation-default:  0.5s ease;
    --animation-fast:  0.3s ease;
    --animation-very-fast:  0.1s ease;
    --sidebar-width: min(35%, 250px);
    --dropdown-box-shadow: inset -15px 15px 20px -30px rgb(47 52 61 / 55%);
    --default-border: 1px solid #e6e6e6;
    --elements-border: 1px solid #b9b8b8;
    --elements-box-shadow:  0px 0px 8px 1px #ddd;
  }


  .Resizer {
    background: #000;
    opacity: 0.2;
    z-index: 1;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -moz-background-clip: padding;
    -webkit-background-clip: padding;
    background-clip: padding-box;
  }
   
  .Resizer:hover {
    -webkit-transition: all 2s ease;
    transition: all 2s ease;
  }
   
  .Resizer.horizontal {
    height: 11px;
    margin: -5px 0;
    border-top: 5px solid rgba(255, 255, 255, 0);
    border-bottom: 5px solid rgba(255, 255, 255, 0);
    cursor: row-resize;
    width: 100%;
  }
   
  .Resizer.horizontal:hover {
    border-top: 5px solid rgba(0, 0, 0, 0.5);
    border-bottom: 5px solid rgba(0, 0, 0, 0.5);
  }
   
  .Resizer.vertical {
    width: 11px;
    margin: 0 -5px;
    border-left: 5px solid rgba(255, 255, 255, 0);
    border-right: 5px solid rgba(255, 255, 255, 0);
    cursor: col-resize;
  }
   
  .Resizer.vertical:hover {
    border-left: 5px solid rgba(0, 0, 0, 0.5);
    border-right: 5px solid rgba(0, 0, 0, 0.5);
  }
  .Resizer.disabled {
    cursor: not-allowed;
  }
  .Resizer.disabled:hover {
    border-color: transparent;
  }

  .pane1Style{
    width: 40%
  }